import React, { useEffect, useRef } from 'react';
import { Link } from 'react-router-dom';
import SimpleReactLightbox from 'simple-react-lightbox';
import { SRLWrapper, useLightbox } from 'simple-react-lightbox';
import Header from '../components/Header';
import Footer from '../components/Footer';
import ReviewsCarousel from '../components/ReviewsCarousel';

//Images..
import bgimg from './../images/background/main-background.jpg';
import bg16 from './../images/background/bg16.jpg';
import about1 from './../images/aman.jpg';
import bg1 from './../images/background/bg1.jpg';
import { RecentProjectDetails } from '../data/RecentProjectDetails';


//Light Gallery on icon click 
const Iconimage = props => {
    const { openLightbox } = useLightbox()
    return (
        <Link to={"#"} onClick={() => openLightbox(props.imageToOpen)} className="mfp-link" >
            <i className="ti-fullscreen icon-bx-xs"></i>
        </Link>
    )
}


const HomePage = () => {
    const aboutProgressRef = useRef(null);

    useEffect(() => {
        // This code will be executed when the component mounts
        var i = 0;

        // Placeholder Animation Start
        var inputSelector = document.querySelectorAll('input, textarea');

        for (i = 0; i < inputSelector.length; i++) {
            inputSelector[i].addEventListener('focus', function (event) {
                return this.parentElement.parentElement.classList.add("focused");
            });
        }

        for (i = 0; i < inputSelector.length; i++) {
            inputSelector[i].addEventListener('blur', function (event) {
                var inputValue = this.value;
                if (inputValue === '') {
                    this.parentElement.parentElement.classList.remove('filled');
                    this.parentElement.parentElement.classList.remove('focused');
                } else {
                    this.parentElement.parentElement.classList.add('filled');
                }
            });
        }
    }, []);
    return (
        <>
            <Header />
            <div className="page-content bg-white rubik">
                <div className="home-banner" style={{ backgroundImage: "url(" + bgimg + ")" }}>
                    <div className="home-bnr-inner">
                        <div className="home-bnr-content">
                            <h2 className="dz-title">Aman Chaudhary</h2>
                            <h4 className="sub-title">Full Stack Developer, WebGIS Developer</h4>
                            <div className="home-bnr-btns">
                                {/* <Link to={"#"} className="site-button white btn-icon"></Link> */}
                                <Link
                                    to="#"
                                    className="site-button white btn-icon"
                                    onClick={(e) => {
                                        e.preventDefault();
                                        aboutProgressRef.current.scrollIntoView({
                                            behavior: 'smooth',
                                            block: 'start',
                                        });
                                    }}
                                >
                                    Know more <i className="fa fa-angle-double-right"></i>


                                </Link>
                            </div>
                        </div>
                    </div>
                </div>

                <div className="content-block">
                    <div className="section-full content-inner about-progress" ref={aboutProgressRef}>
                        <div className="container">
                            <div className="row">
                                <div className="col-md-12 col-lg-6">
                                    <h2 className="font-weight-400">Hi, I’m Aman Chaudhary.<br /><p className="font-weight-300" style={{ fontSize: "20px" }}> Senior Undergraduate | IIT Gandhinagar</p></h2>
                                    {/* <div className="progress-section">
                                            <div className="progress-bx">
                                                <h4>Hard Work</h4>
                                                <div className="count-box">70%</div>
                                                <div className="progress">
                                                    <div className="progress-bar bg-success" style={{width:'70%'}} role="progressbar"></div>
                                                </div>
                                            </div>
                                            <div className="progress-bx">
                                                <h4>Projects Delivery</h4>
                                                <div className="count-box">80%</div>
                                                <div className="progress">
                                                    <div className="progress-bar bg-success" style={{width:'80%'}} role="progressbar"></div>
                                                </div>
                                            </div>
                                            <div className="progress-bx">
                                                <h4>Customers Love</h4>
                                                <div className="count-box">90%</div>
                                                <div className="progress">
                                                    <div className="progress-bar bg-success" style={{width:'90%'}} role="progressbar" ></div>
                                                </div>
                                            </div>
                                        </div> */}
                                    {/* <h3 className="m-b10">The Project</h3> */}
                                    <p style={{ fontSize: "18px" }}>As a passionate Full Stack Developer, I specialize in front-end technologies like ReactJS and NextJS, and I'm proficient in back-end development using Flask and NodeJS. My portfolio showcases a range of innovative web applications, blending creativity with technical prowess to deliver seamless user experiences. With a strong desire to collaborate and create impactful digital solutions, I'm excited to turn ideas into reality and make a difference in the world of web development. Let's work together to build remarkable projects!.</p>
                                </div>
                                <div className="col-md-12 col-lg-6 d-flex ">
                                    <div className="img-half-bx align-items-stretch">
                                        <img src={about1} alt="" />
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>


                    <div className="section-full content-inner-1 overlay-primary about-service bg-img-fix" style={{ backgroundImage: "url(" + bg1 + ")" }}>
                        <div className="container">
                            <div className="section-head text-white text-center">
                                <h2 className="box-title m-tb0 max-w800 m-auto">My Expertise<span className="bg-primary"></span></h2>
                                {/* <p>Contrary to popular belief, Lorem Ipsum is not simply random text. It has roots in a piece of classical Latin literature from 45 BC, making it over</p> */}
                            </div>
                        </div>
                        <div className="container">
                            <div className="row text-white">

                                <div className="col-lg-4 col-md-4 col-sm-6 m-b30" >
                                    <div className="icon-bx-wraper bx-style-1 p-a30 center">
                                        <div className="icon-lg text-white m-b20"> <Link to={"#"} className="icon-cell text-white"><i className="flaticon-devices" /></Link> </div>
                                        <div className="icon-content">
                                            <h5 className="dlab-tilte text-uppercase">Software Development</h5>
                                            <p>Lorem ipsum dolor sit amet, consectetuer adipiscing elit, sed diam nonummy nibh euismod..</p>
                                        </div>
                                    </div>
                                </div>

                                <div className="col-lg-4 col-md-4 col-sm-6 m-b30">
                                    <div className="icon-bx-wraper bx-style-1 p-a30 center">
                                        <div className="icon-lg text-white m-b20"> <Link to={"#"} className="icon-cell text-white"><i className="flaticon-devices" /></Link> </div>
                                        <div className="icon-content">
                                            <h5 className="dlab-tilte text-uppercase">Frontend Dev React, NextJS</h5>
                                            <p>Passionate about UI/UX. Over 4 years of development experience in HTML, CSS, JS, React and NextJS frameworks.</p>
                                        </div>
                                    </div>
                                </div>

                                <div className="col-lg-4 col-md-4 col-sm-6 m-b30">
                                    <div className="icon-bx-wraper bx-style-1 p-a30 center">
                                        <div className="icon-lg text-white m-b20"> <Link to={"#"} className="icon-cell text-white"><i className="flaticon-devices" /></Link> </div>
                                        <div className="icon-content">
                                            <h5 className="dlab-tilte text-uppercase">Software Development</h5>
                                            <p>Lorem ipsum dolor sit amet, consectetuer adipiscing elit, sed diam nonummy nibh euismod..</p>
                                        </div>
                                    </div>
                                </div>

                            </div>
                        </div>

                    </div>



                    <div className="section-full content-inner bg-white workspace-bx mfp-gallery">
                        <div className="container">
                            <div className="row">
                                <div className="col-lg-12">
                                    <div className="section-head text-center">
                                        <h2 className="font-weight-300">My Recent Work<p style={{ fontSize: "18px" }}>Here are a few past projects I've worked on.</p></h2>

                                    </div>
                                </div>
                            </div>
                            <SimpleReactLightbox>
                                <SRLWrapper >
                                    <div className="row">
                                        {RecentProjectDetails.map((project, index) => (
                                            <div className="col-lg-4 col-md-12 col-sm-12" key={index}>
                                                <div className="dlab-box portfolio-box m-b30">
                                                    <div className="dlab-media dlab-img-effect dlab-img-overlay1" data-tilt=""> <img src={project.Image} alt="" />
                                                        <div className="overlay-bx">
                                                            <div className="overlay-icon text-white">
                                                                <h5>{project.Name}</h5>
                                                                {/* <p className="m-b10">{project.TagLine}</p> */}
                                                                <Iconimage />
                                                                <a href={project.Link} target="bank"><i className="ti-arrow-top-right icon-bx-xs"></i></a>
                                                            </div>
                                                        </div>
                                                    </div>
                                                </div>
                                            </div>
                                        ))}
                                    </div>
                                    <div className="col-lg-12 m-t20 text-center">
                                        <Link to="/portfolio" className="site-button long-btn radius-xl shadow">See More</Link>
                                    </div>
                                </SRLWrapper >
                            </SimpleReactLightbox>
                        </div>
                    </div>

                    <div className="section-full content-inner-2 bg-img-fix overlay-primary gradient testimonial-curv-bx m-b20" style={{ backgroundImage: "url(" + bg1 + ")" }}>
                        <div className="container">
                            <div className="row">
                                <div className="col-lg-2"></div>
                                <div className="col-lg-8">
                                    <div className="section-head text-center">
                                        <h2 className="text-uppercase"><span className="font-weight-300">Happy Clients</span> <br /> Project Reviews</h2>
                                        <h5 className="font-weight-300 text-gray-dark">People I've worked with have said some nice things...</h5>
                                    </div>
                                    <ReviewsCarousel />
                                </div>
                            </div>
                        </div>
                    </div>


                    <div className="section-full content-inner-2 bg-img-fix overlay-primary gradient subscribe-bx" style={{ backgroundImage: "url(" + bg1 + ")" }}>
                        <div className="container">
                            <form className="row text-white dezPlaceAni align-items-center dzSubscribe" action="script/mailchamp.php" method="post">
                                <div className="col-lg-4  col-md-12">
                                    <h2 className="m-b0"> Start a project </h2>
                                </div>
                                <div className="col-lg-6 col-md-9 contact-form-bx">
                                    <p style={{ fontSize: "20px" }}> Interested in working together? We should queue up a time to chat. </p>

                                </div>
                                <div className="col-lg-2 col-md-3">
                                    <Link to="/contact" ><button className="site-button button-md radius-xl white btn-block">Let's do this</button></Link>
                                </div>
                            </form>
                        </div>
                    </div>


                    <div className="section-full content-inner-2 bg-white contact-form-bx" style={{ backgroundImage: "url(" + bg16 + ")", backgroundSize: "100%" }}>
                        <div className="container">
                            <div className="section-head text-center">
                                <h2 className="text-uppercase"><span className="font-weight-300">Contact </span> us</h2>
                            </div>
                            <div className="dezPlaceAni">
                                <div className="dzFormMsg"></div>
                                <form method="post" className="" action="">
                                    <input type="hidden" value="Contact" name="dzToDo" />
                                    <div className="row">
                                        <div className="col-lg-4 col-md-5 col-sm-12">
                                            <div className="form-group">
                                                <div className="input-group">
                                                    <label>Your Name</label>
                                                    <input name="dzName" type="text" required className="form-control" placeholder="" />
                                                </div>
                                            </div>
                                            <div className="form-group">
                                                <div className="input-group">
                                                    <label>Your Email Address</label>
                                                    <input name="dzEmail" type="email" className="form-control" required placeholder="" />
                                                </div>
                                            </div>
                                            <div className="form-group">
                                                <div className="input-group">
                                                    <label>Phone</label>
                                                    <input name="dzOther[Phone]" type="text" required className="form-control" placeholder="" />
                                                </div>
                                            </div>
                                        </div>
                                        <div className="col-lg-8 col-md-7 col-sm-12">
                                            <div className="form-group">
                                                <div className="input-group">
                                                    <label>Your Message...</label>
                                                    <textarea name="dzMessage" rows="4" className="form-control" required placeholder=""></textarea>
                                                </div>
                                            </div>
                                        </div>
                                        <div className="col-lg-12 col-md-12 col-sm-12 text-center">
                                            <button name="submit" type="submit" value="Submit" className="site-button outline outline-2 radius-xl button-md m-t10">Submit Now</button>
                                        </div>
                                    </div>
                                </form>
                            </div>
                        </div>
                    </div>
                    {/* <!-- Contact Us End --> */}
                </div>
                {/* <!-- contact area END --> */}
            </div >

            <Footer />
        </>
    )
}

export default HomePage